.content{
  position: relative;
  color: #fff;
}

.content h2{
  position: absolute;
  color: #111;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 25px;
}

.content h2:nth-child(1)
{
  color: transparent;
  -webkit-text-stroke: 2px rgb(255, 255, 255);
  
  
}

.content h2:nth-child(2)
{
  color: #111;
  -webkit-animation: animate 4s ease-in-out infinite;
          animation: animate 4s ease-in-out infinite;
}


@-webkit-keyframes animate{
  0%, 100%
  {
      /* clip-path: polygon(0% 5%, 15% 44%, 32%  50%, 54% 60%, 70% 61%, 84% 59%, 100% 52%, 100% 100%, 0% 100%); */
      /* clip-path: polygon(1% 99%, 0% 50%, 31% 33%, 67% 41%, 93% 61%, 100% 99%); */
      -webkit-clip-path: polygon(0% 100%, 0% 0%, 37% 13%, 61% 28%, 81% 54%, 100% 100%);
              clip-path: polygon(0% 100%, 0% 0%, 37% 13%, 61% 28%, 81% 54%, 100% 100%);

  }
  50%{
   
    -webkit-clip-path: polygon(0% 99%, 11% 71%, 27% 48%, 51% 31%, 100% 1%, 99% 100%);
   
            clip-path: polygon(0% 99%, 11% 71%, 27% 48%, 51% 31%, 100% 1%, 99% 100%);

      /* clip-path: polygon(0% 75%,16% 65%, 34%  66%, 51% 62%, 67% 50%, 84% 45%, 100% 25%, 100% 100%, 0% 100%); */

  }
  
}


@keyframes animate{
  0%, 100%
