/* Start of the search button styling */
.box{
  max-width: 400px;
  width: 100%;

}
.box .search-box{
  position: relative;
  height: 50px;
  max-width: 48px;
  
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);
  border-radius: 25px;
  transition: all 0.3s ease;
}
#check:checked ~ .search-box{
  max-width: 330px;
}
.search-box input{
 position: absolute;
 height: 90%;
 width: 90%;
 border-radius: 25px;
 background: #fff;
 outline: none;
 border: none;
 padding-left: 7px;
 font-size: 18px;
 box-shadow: 0px 3px 3px rgb(0, 0, 0);
}
.search-box .icon{
  position: absolute;
  right: -7px;
  top: 0;
  width: 55px;
  background: #FFF;
  height: 95%;
  text-align: center;
  line-height: 45px;
  color: #FF676D;
  font-size: 20px;
  border-radius: 25px;
  box-shadow: 0px 3px 3px rgb(0, 0, 0);
}
#check:checked ~ .search-box .icon{
  background: #FF676D;
  color: #FFF;
  width: 60px;
  border-radius: 0 25px 25px 0;
  
}
#check{
  display: none;
}



